import React from 'react';
import { Slots, Slot } from './styledComponents';

const fabrics = [
  'dentelle',
  'cuir',
  'soie/mousseline',
  'tailleur',
  'maille',
  'velour',
  'aucun',
];

class FabricSelector extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <Slots>
        {fabrics.map(fabric =>
          <Slot
            isSelected={this.state[fabric]}
            onClick={() => this.setState({ [fabric]: !this.state[fabric] })}
            key={fabric}
          >{fabric}</Slot>,
        )}
      </Slots>
    );
  }
}

export default FabricSelector;



